import React, { Component } from "react";

import { Page } from "react-tabler-components";

import SiteWrapper from "../../SiteWrapper";

class Icons extends Component {
  render() {
    return (
      <SiteWrapper>
        <Page.Content>
          <Page.Header>Icons</Page.Header>
        </Page.Content>
      </SiteWrapper>
    );
  }
}
export default Icons;



