import React, { Component } from "react";

import { Page } from "react-tabler-components";

import SiteWrapper from "../SiteWrapper";

class Forms extends Component {
  render() {
    return (
      <SiteWrapper>
        <Page.Content>
          <Page.Header>Forms</Page.Header>
        </Page.Content>
      </SiteWrapper>
    );
  }
}
export default Forms;



